//  React   useState,   css
import React, {useState} from 'react';
import './Calculator.scss';

function Calculator() {
  //
  const [num1, setNum1] = useState('');
  const [num2, setNum2] = useState('');
  const [result, setResult] = useState('');
  const [isModalOpen, setIsModalOpen] = useState(false);

  //  
  const handleInputChange = (e, inputType) => {
    const inputValue = e.target.value;
    if (inputType === 'num1') {
      setNum1(inputValue);
    } else if (inputType === 'num2') {
      setNum2(inputValue);
    }
  };

  //  ,    ,   
  const handleCalculation = (operator) => {
    if (!num1 || !num2) {
      setIsModalOpen(true);
      return;
    }

    // 
    const num1Value = parseFloat(num1);
    const num2Value = parseFloat(num2);

    //  map
    const operationsMap = {
      '+': (a, b) => (a + b),
      '-': (a, b) => (a - b),
      '': (a, b) => (a * b),
      '/': (a, b) => (b !== 0 ? (a / b) : ':   '), // 
    };

    //  
    const calculatedResult = operationsMap[operator](num1Value, num2Value);

    //   n   ,      0,1+0,2=0,300...04
    const roundToNDecimals = (number, n) => {
      return Number(number.toFixed(n));
    };

    //  10    (     )
    const resultFormatted = calculatedResult !== ':   ' ? roundToNDecimals(calculatedResult, 10) : calculatedResult;

    setResult(resultFormatted);

    //   
    setNum1('');
    setNum2('');
  };

  //  ,    4   ,     
  const operations = ['+', '-', '', '/'];

  //  ,    
  const handleCloseModal = () => {
    setIsModalOpen(false);
  };

  //,   :
  //    calculator-container
  // 1 input-field
  // 2 input-field
  //     operator-button
  // dialog    backdrop (    css)
  // result-field

  return (
    <div className="calculator-container">
      <input
        type="number"
        className="input-field"
        placeholder=" "
        value={num1}
        autoFocus
        onChange={(e) => handleInputChange(e, 'num1')}
      />

      <input
        type="number"
        className="input-field"
        placeholder=" "
        value={num2}
        onChange={(e) => handleInputChange(e, 'num2')}
      />

      <div>
        {operations.map((operation) => (
          <button className="operator-button" key={operation} onClick={() => handleCalculation(operation)}>
            {operation}
          </button>
        ))}
      </div>

      {isModalOpen && <div className="backdrop"/>}

      <dialog open={isModalOpen} className="dialog">
        <p>  </p>
        <button onClick={handleCloseModal}></button>
      </dialog>

      <input
        type="text"
        className="result-field"
        placeholder=":"
        value={result}
        readOnly/>
    </div>
  );
}

export default Calculator;
